const Home = r => require(['../views/home'], r);
const Trip = r => require(['../views/trip'], r);

const Login = r => require(['../views/login'], r);
const Register = r => require(['../views/register'], r);
const Forgetpwd = r => require(['../views/forgetpwd'], r);
const User = r => require(['../views/ucenter/index'], r);
const UserHome = r => require(['../views/ucenter/userHome'], r);
const Setting = r => require(['../views/ucenter/setting'], r);

const NotFound = r => require(['../views/notfound'], r);

// 根目录
const rootPath = '';

// 页面路由
const routes = [
  {path: '', redirect: {name: 'home'}},
  {path: '/', component: Home, name: 'home'},
  {path: '/trip', component: Trip, name: 'trip'},
  {path: '/login', component: Login, name: 'login', meta: {bodyClass: 'reg-bg'}},
  {path: '/register', component: Register, name: 'register', meta: {bodyClass: 'reg-bg'}},
  {path: '/forgetpwd', component: Forgetpwd, name: 'forgetpwd', meta: {bodyClass: 'reg-bg'}},
  {path: '/ucenter', component: User, meta: { requiresAuth: true },
		children: [
			{path: '', component: UserHome, name: 'userhome'},
			{path: 'setting', component: Setting, name: 'setting'},
		]	
	}
].map(route => {
  route.path = rootPath + route.path;
  return route;
});

// 404 页
routes.push({path: '*', component: NotFound, name: 'notfound'});

export default routes;



