<template>
  <div class="weui_loading_toast" v-show="show" 
      :class="{'loading_top': position == 'top','loading_bottom': position == 'bottom', 'loading_center': position == 'center'}">
    <div class="weui_mask_transparent"></div>
    <div class="weui_toast">
      <div class="weui_loading">
      </div>
      <p class="weui_toast_content">{{text}}<slot></slot></p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    show: Boolean,
    text: {
      type: String,
      default: 'Loading'
    },
    position: {
      type: String,
      default: 'top'
    }
  }
}
</script>
<style lang="scss" scoped>
.vux-loading {
  animation-duration: 0.6s;
  animation-iteration-count: infinite;
  animation-name: vux-loading;
  animation-timing-function: linear;
  border-radius: 99em;
  border: 3px solid #DDD;
  border-left-color: #666;
  display: inline-block;
  width: 16px;
  height: 16px;
  border-width: 2px;
  display:table-cell;
  vertical-align:middle;
}

@keyframes vux-loading {
  from {
    transform: rotate(0deg)
  }
  to {
    transform: rotate(360deg)
  }
}

.weui_toast {
    position: fixed;
    z-index: 50000;
    width: 7.6em;
    min-height: 7.6em;
    top: 180px;
    left: 50%;
    margin-left: -3.8em;
    background: rgba(40, 40, 40, 0.75);
    text-align: center;
    border-radius: 5px;
    color: #FFFFFF;
}
.weui_icon_toast {
    margin: 22px 0 0;
    display: block;
    &:before {
        content: '\EA08';
        color: #FFFFFF;
        font-size: 55px;
    }
}
.weui_toast_content {
    margin: 0 0 15px;
}

// loading toast
.weui_loading_toast{
    .weui_toast_content{
      margin-top:64%;
      font-size:14px;
    }
}
.weui_loading{
    position: absolute;
    width: 0px;
    z-index: 1;
    left: 50%;
    top: 0;
    margin: 20px 0 0 -15px;
    width: 30px;
    height: 30px;
    border-right: 3px solid #f60;
    border-bottom: 3px solid transparent;
    border-radius: 100%;
    animation: tail-spin 0.8s linear infinite;
}
@keyframes tail-spin {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
</style>


