<!--suppress ALL -->
<template>
  <div id="app">
    <loading :show="isLoading" :text="message"></loading>
    <router-view></router-view>
  </div>
</template>

<script>
  import {mapState,mapGetters} from 'vuex';
  import loading from './components/loading.vue';

  export default {
    computed: {
      ...mapGetters({
        isLoading: 'getLoading',
        message: 'getMessage'
      })
    },
    mounted () {

    },
    components: {
      loading
    }
  }
</script>
<style lang="scss">
@import './assets/index.scss';
@import '../node_modules/vue-toast-mobile/lib/index.css';
#app {
  max-width: 750px;
  margin: 0 auto;
  overflow: hidden;
}
.flexbox{
  @include flexbox('.flex',left);
}
.gray-bg{
  background-color: #eaeaea;
}
.reg-bg {
  background-color: $themes-color;
  color: #fff;
}
.register-block .child-view{
  left: 0;
  padding: 0 $inner-width;
}
.child-view{
  position: absolute;
  top:0;
  width: 100%;
}
.child-view,.transition{
  transition: all .5s cubic-bezier(.55,0,.1,1);
}
.slide-left-enter, .slide-right-leave-active{
  opacity: 0;
  -webkit-transform: translate(30px, 0);
  transform: translate(30px, 0);
}
.slide-left-leave-active, .slide-right-enter{
  opacity: 0;
  -webkit-transform: translate(-30px, 0);
  transform: translate(-30px, 0);
}
</style>



